.color-options {
    font-family: MetropolisRegular;
    letter-spacing: 0.03em;
    display: flex;
    justify-content: flex-end;
    margin-right: 20px; /* Adjust the spacing as needed */
    position: absolute;
    top: 400px; /* Adjust the vertical position as needed */
    right: 300px;
  }
  
  .color-option {
    font-size: 20px;
    font-weight: bold;
    padding: 5px 10px;
    margin-right: 10px;
    cursor: pointer;
  }
  .dark .color-option {
    color: #e0e0e0;
  }
  
  
  .color-option.selected {
    color: #000;
  }
  .dark .color-option.selected {
    color: #e0e0e0;
  }
  
  .color-option.not-selected {
    color: #a5a5a5;
  }

  
