  .fade-in-btn {
    animation: fadeInAnimation 0.5s ease; /* Add a fade-in animation with a duration of 0.5 seconds */
  }
  
  @keyframes fadeInAnimation {
    from {
      opacity: 0; /* Start with opacity 0 */
    }
    to {
      opacity: 1; /* Fade in to opacity 1 */
    }
  }

  .fade-out-btn {
    animation: fadeOutAnimation 0.5s ease; /* Add a fade-out animation with a duration of 0.5 seconds */
  }

  @keyframes fadeOutAnimation {
    from {
      opacity: 1; /* Start with opacity 1 */
    }
    to {
      opacity: 0; /* Fade out to opacity 0 */
    }
  }

