  @keyframes rotateText {
    0% {
      transform: rotate(360deg);
    }
  }

  .circle-text-holder1 {
    position: relative;
    left: 50%;
  }

  .circle-text1 {
    position: absolute;
    width: 0%;
    height: 100%;
    animation: 
      fadeInAnimation 3s ease-in,
      rotateText 10s linear infinite;
    font-family: MetropolisRegular;
    font-weight: 400;
  }
  .circle-text1 span {
    position: absolute;
    left: 100%;
    font-size: 1.2em;
    transform-origin: 0px 200px;
  }
  .dark .circle-text1 {
    color: #e0e0e0;
  }

  .snake-background-image {
    background-image: url(../images/snake_background_white.png);
    background-size: cover; /* Ensure the background image covers the entire viewport */
    background-repeat: no-repeat; /* Prevent the background image from repeating */
    background-attachment: fixed; /* Fix the background image in place */
    height: 100%;
    width: 100%;
  }
  .dark .snake-background-image {
    background-image: url(../images/snake_background_black.png);
  }

  .snake-background-container {
    margin-top: 10%;
    margin-left: 36%;
    width: 100%;
    height: 100%;
    /* z-index: -9999999; */
  }

  .snake-background-item {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }

  .snake-background-item img {
    position: absolute;
    width: 60vh;
    height: 100vh;
    /* object-fit: cover; */
    transform: scale(1.5);
    transition: 2s cubic-bezier(0.075, 0.82, 0.165, 1);
  }
  .dark .snake-background-item img {
    filter: invert();
  }
  
  .snake-background-overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #e0e0e0;
    transition: 1s cubic-bezier(0.075, 0.82, 0.165, 1);
  }
  .dark .snake-background-overlay {
    background-color: #0F0F10;
  }

  .transition-active .snake-background-overlay {
    top: -100%;
  }
  
